.success{
    color: green;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    border-color: green;
    padding: 10px;
    margin-bottom: 10px;
    background-color: lightgray;
}
.error{
    color: red;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    border-color: red;
    padding: 10px;
    margin-bottom: 10px;
    background-color: lightgray;
}